import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, LoadingController } from 'ionic-angular';
import { Http } from '@angular/http';
import { AppConfig } from '../../../app/app.config';
import 'rxjs/add/operator/map';
/**
 * Generated class for the StudymeansPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
    selector: 'page-studymeans',
    templateUrl: 'studymeans.html',
})
export class StudymeansPage {
    url = AppConfig.getUrl() + "/artical/page?pageSize=10&type=1&partyNo=" + localStorage.partyNo
    arr: any;
    pageNo = 0;

    constructor(public navCtrl: NavController, public navParams: NavParams, public http: Http, public alertCtrl: AlertController, public loadingCtrl: LoadingController) {
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad StudymeansPage');
        this.get()
    }

    get(event?) {
        if (event) {
            this.pageNo++;
        } else {
            this.pageNo = 0;
            this.arr = [];
        }
        let loading = this.loadingCtrl.create({
            content: '加载中...'
        });
        loading.present();
        return new Promise((resolve, reject) => {
            this.http.get(this.url + "&pageNumber="+ this.pageNo)
                .map(res => res.json())
                .subscribe(data => {
                    resolve(data);
                    if (event)
                        event.complete();
                    if (data.status) {
                        for (let i = 0; i < data.data.length; i++) {
                            this.arr.push({
                                id: i + (10 * this.pageNo),
                                title: data.data[i].title,
                                attachments: data.data[i].attachments[0],
                                descs: data.data[i].descs,
                                createdAt: new Date(data.data[i].createdAt).getFullYear() + "-" + (new Date(data.data[i].createdAt).getMonth() * 1 + 1) + "-" + new Date(data.data[i].createdAt).getDate(),
                            })
                        }
                        console.log(this.arr)
                    } else {
                        let alert = this.alertCtrl.create({
                            title: data.msg
                        });
                        alert.present();
                    }
                }, err => {
                    let alert = this.alertCtrl.create({
                        title: err
                    });
                    alert.present();
                })
            loading.dismiss();
        })
    }
    goto(id){
        this.navCtrl.push("StudyarticlePage",{article:this.arr[id]})
    }
}



